import React, { useEffect } from 'react'
import AniLink from 'gatsby-plugin-transition-link/AniLink'
import styled from 'styled-components'
import { useStaticQuery, graphql } from "gatsby"

const StyledMenu = styled.div`
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  transform: ${props =>
    props.isMenuOpen ? 'translateX(0)' : 'translateX(-100%)'};
  background-color: rgba(0, 0, 0, 1);
  transition: transform 0.3s ease-in-out;
  z-index: 1;
`

const StyledLink = styled(AniLink)`
  text-decoration: none;
  color: white;
  background: black;
`

const OverlayMenu = ({ isMenuOpen, setIsMenuOpen }) => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            address
            city
            email
            copyright
          }
        }
      }
    `
  )

  useEffect(() => {
    isMenuOpen
      ? (document.body.style.overflow = 'hidden')
      : (document.body.style.overflow = 'unset')
  }, [isMenuOpen])

  return (
    <StyledMenu isMenuOpen={isMenuOpen}>
      <div className="overlay-menu">
        <div className="overlay-menu__left">
          <nav>
            <ul className="overlay-menu__list">
              <li className={`${isMenuOpen && 'show'} overlay-menu__list-item`}>
                <div>
                  <StyledLink cover bg={'black'} direction="right" to="/">
                    Home
                  </StyledLink>
                </div>
              </li>
              {/* <li className={`${isMenuOpen && 'show'} overlay-menu__list-item`}>
                <div>
                  <StyledLink cover bg={'black'} direction="right" to="/blog/">
                    Blog /
                  </StyledLink>
                </div>
              </li>
              <li className={`${isMenuOpen && 'show'} overlay-menu__list-item`}>
                <div>
                  <StyledLink
                    cover
                    bg={'black'}
                    direction="right"
                    to="/projects/"
                  >
                    Projects
                  </StyledLink>
                </div>
              </li> */}
            </ul>
          </nav>
          <div className="overlay-menu__info">
            <div className="animation-container">
              <p className={`${isMenuOpen && 'show'} up`}>{site.siteMetadata.address}</p>
            </div>
            <div className="animation-container">
              <p className={`${isMenuOpen && 'show'} down`}>{site.siteMetadata.city}</p>
            </div>
            <div className="animation-container">
              <p className={`${isMenuOpen && 'show'} up`}>
                <a href={`mailto:${site.siteMetadata.email}`}>
                  {site.siteMetadata.email}
                </a>
              </p>
            </div>
            <div className="animation-container">
              <p className={`${isMenuOpen && 'show'} down`}>
                {site.siteMetadata.copyright}
              </p>
            </div>
          </div>
        </div>
        <div className="overlay-menu__right"></div>
      </div>
    </StyledMenu>
  )
}

export default OverlayMenu
